
import PanelCart from './panel-cart.vue'
import api from '../api/index.js'
import * as config from '../config/config.js'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
  name: 'cart',
  data: function() {
    return {
      type: '1',
      cart_data:[],
    }
  },
  created:function(){
    this.fetch_data();
    //
  },
  methods: {
    handleEvents:function(){

    },
    handle_delete:function(){
      
    },
    fetch_data:function(){
      var self=this;
      api.get_cart_data().then(res=>{
        console.log(res);
        if(res.ok){
          if(res.data.data.length){
            self.cart_data=res.data.data;
          }else{
            self.$vux.toast.show({
                text:'<span class="font-normal"></span>',
                type:'text'
            });
          }
        }else if(res.status=="401"&&!!window.localStorage.refresh_token&&!!window.localStorage.client_id&&window.localStorage.client_secret){
          self.$router.push('cart');
        }
      })
    },
    submit_order:function(){
      console.log("");
    }
  },
  components: {
    PanelCart,
    Flexbox,
    FlexboxItem,
    XButton,
    Swipeout,
    SwipeoutItem,
    SwipeoutButton,
    Sticky,
    Tabbar,
    TabbarItem
  }
}



